export default {
  name: 'PressSection',
  props: {
    type: {
      type: String,
      default: '',
    },
    title: {
      type: String,
      required: true,
      default: '',
    },
    titleFontSize: {
      type: String,
      default: '14px',
    },
    titleColor: {
      type: String,
      default: '#333',
    },
    subTitle: {
      type: String,
      default: '',
    },
    subTitleFontSize: {
      type: String,
      default: '12px',
    },
    subTitleColor: {
      type: String,
      default: '#999',
    },
    padding: {
      type: [Boolean, String],
      default: false,
    },
    headerStyle: {
      type: String,
      default: '',
    },
    active: {
      type: Boolean,
      default: true,
    },
    clickable: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['click'],
  computed: {
    innerPadding() {
      if (typeof this.padding === 'string') {
        return this.padding;
      }

      return this.padding ? '10px' : '';
    },
  },
  methods: {
    onClick() {
      this.$emit('click');
    },
  },
};
