import PressButton from '../press-button/press-button.vue';
import PressField from '../press-field/press-field.vue';
import PressDemoBlock from '../press-demo-block/press-demo-block.vue';


export default {
  name: 'PressDemoInput',
  i18n: {
    'zh-CN': {
      custom: '',
      mockData: '',
    },
    'en-US': {
      custom: 'Custom',
      mockData: 'Custom Data',
    },
  },
  components: {
    PressField,
    PressButton,
    PressDemoBlock,
  },
  props: {
    value: {
      type: String,
      default: '',
    },
    sectionStyle: {
      type: String,
      default: 'margin: 0;background: #f7f8fa;',
    },
    headerStyle: {
      type: String,
      default: 'background: #f7f8fa;',
    },
    autosize: {
      type: [Boolean, Object],
      default: () => ({ maxHeight: 600, minHeight: 50 }),
    },
  },
  data() {
    return {
      inputValue: this.value,
    };
  },
  watch: {
    value: {
      handler(val) {
        this.inputValue = val;
      },
    },
  },
  mounted() {

  },
  methods: {
    onClearInput() {
      this.inputValue = '';
    },
    onConfirmInput() {
      this.$emit('confirm', this.inputValue);
    },
  },
};
